import React, { Component } from 'react';
import './quote.css'



 export default class quote extends Component{
    render(){
        return(
            <div>
                <p>{this.props.quote}</p>
                <p>{this.props.author}</p>
                <button className="btn btn-default" onClick={this.props.update}>Change</button>
                <button className="btn btn-default" onClick={this.props.share}><i className="fa fa-twitter"></i> Tweet</button>
            </div>
        )
    }
}